<template>
    <div ref="editorWang" style="text-align: left"></div>
</template>

<script>
import wangEditor from "wangeditor";
export default {
    data() {
        return {
            editor: null, // editor
            editorData: "", // 
        };
    },
    props:['currenEditor'],
    watch:{
        currenEditor(newVal,oldVal){
            this.editor.txt.html(newVal)
        }
    },
    mounted() {
        this.editor = new wangEditor(this.$refs.editorWang);
        //  onchange  vue 
        this.editor.config.onchange = (newHtml) => {
            this.editorData = newHtml;
            this.$emit("onEditor",this.editorData)
        };

        // 
        this.editor.config.menus = [
            "head", // 
            "bold", // 
            "fontSize", // 
            "fontName", // 
            "italic", // 
            "underline", // 
            "strikeThrough", // 
            "foreColor", // 
            "backColor", // 
            "link", // 
            "list", // 
            "justify", // 
            "quote", // 
            "emoticon", // 
            "image", // 
            "table", // 
            "code", // 
            "undo", // 
            "redo", // 
        ];

        this.editor.create(); // 
    },
    beforeDestroy() {
        //  API 
        this.editor.destroy();
        this.editor = null;
    },
};
</script>

